import "@servicenow/now-button";
import { LEFT, RIGHT, BOTH } from "./constants";
import { LEFT_BTN_CLICKED, MIDDLE_BTN_CLICKED, RIGHT_BTN_CLICKED } from "./actions";

export const ActionBar = ({ dispatch, displayedPane, config }) => {
	const {leftIcon, middleIcon, rightIcon, leftTooltip, middleTooltip, rightTooltip} = config;

	const leftButtonHandler = () => {
		dispatch(LEFT_BTN_CLICKED);
	};
	const middleButtonHandler = () => {
		dispatch(MIDDLE_BTN_CLICKED);
	};

	const rightButtonHandler = () => {
		dispatch(RIGHT_BTN_CLICKED);
	};

	return (
		<div className="action-bar">
			<now-button-stateful
				icon={leftIcon}
				size="md"
				variant="secondary-highlighted"
				configAria={{ "aria-label": "Display left pane" }}
				on-click={leftButtonHandler}
				selected={displayedPane == LEFT}
				tooltip-content={leftTooltip}
			></now-button-stateful>
			<now-button-stateful
				icon={middleIcon}
				size="md"
				variant="secondary-highlighted"
				configAria={{ "aria-label": "Display both panes" }}
				on-click={middleButtonHandler}
				selected={displayedPane == BOTH}
				tooltip-content={middleTooltip}
			></now-button-stateful>
			<now-button-stateful
				icon={rightIcon}
				size="md"
				variant="secondary-highlighted"
				configAria={{ "aria-label": "Display right pane" }}
				on-click={rightButtonHandler}
				selected={displayedPane == RIGHT}
				tooltip-content={rightTooltip}
			></now-button-stateful>
		</div>
	);
};
