import { LEFT, RIGHT, BOTH } from "./constants";
import { ActionBar } from "./ActionBar.js";
import '@servicenow/now-empty-state';

const Divider = ({ disabled, isActive, dividerStyles }) => {
	return (
		<div
			class={{
				"divider-bar" : true,  
				"active": isActive,
				"disabled": disabled
			}}
			style={dividerStyles}
		>
			<div className={isActive ? "handle active" : "handle"}></div>
		</div>
	);
};

const Pane = ({side, width, minWidth}) => {
	return (
		<div
			style={{ width: `${width}%`, minWidth }}
			className={`${side}-pane`}
		>
			<slot name={side}>
				<now-empty-state />
			</slot>
		</div>
	);
};

const PaneContainer = ({disabled, displayedPane, leftWidth, isDragging, rightWidth, dividerStyles, minLeft, minRight}) => {
	return (
			<div className={`pane-container showing-${displayedPane} ${isDragging ? "is-dragging" : ""}`}>
				<Pane width={leftWidth} side={LEFT} minWidth={minLeft}></Pane>
				<Divider isActive={isDragging} dividerStyles={dividerStyles} disabled={disabled}></Divider>
				<Pane width={rightWidth} side={RIGHT} minWidth={minRight}></Pane>
			</div>
	);
};
const getMinimums = (constraints, displayedPane) => {
	const {minPixels, minRatio} = constraints;
	if (displayedPane === BOTH) {
		if (minPixels)
			return `${minPixels}px`;
		if (minRatio)
			return `${minRatio}%`;
	}
	return '';

}
export const view = (state, { dispatch }) => {
	const { isDragging, leftPct, rightPct, displayedPane } = state;
	const { disabled, actionBar: displayActionBar, actionBarConfig, bare, dividerStyles, leftPaneConstraints, rightPaneConstraints } = state.properties;
	return (
		<div className={bare ? "main-container bare" : "main-container"}>
			<PaneContainer
				disabled={disabled}
				displayedPane={displayedPane}
				leftWidth={leftPct}
				rightWidth={rightPct}
				isDragging={isDragging && !disabled}
				dividerStyles={dividerStyles}
				minLeft={getMinimums(leftPaneConstraints, displayedPane)}
				minRight={getMinimums(rightPaneConstraints, displayedPane)}
			></PaneContainer>

			{ displayActionBar && !disabled ? <slot name="action-bar"><ActionBar dispatch={dispatch} config={actionBarConfig} displayedPane={displayedPane}></ActionBar></slot> : null }
		</div>
	);
};
