import { DIVIDER_BAR_WIDTH, LEFT, RIGHT } from '../constants';

export const getPositionPixels = (hostWidth, defaultDividerPosition) => {
	let dividerPositionPixels = 0;
	
	const pxRE = new RegExp('^(-?\\d+)px$');
	if (pxRE.test(defaultDividerPosition)){
		dividerPositionPixels = 0 + parseInt(defaultDividerPosition.slice(0, -2), 10);
		if (dividerPositionPixels > hostWidth)
			dividerPositionPixels = hostWidth;
		if (dividerPositionPixels < 0)
			dividerPositionPixels = hostWidth + dividerPositionPixels;
	} else {
		const parsedDividerPosition = parseInt(defaultDividerPosition, 10)
		dividerPositionPixels = hostWidth * (parseInt(parsedDividerPosition, 10)/100);
	}

	if (isNaN(dividerPositionPixels))
		dividerPositionPixels = hostWidth/2;
	if (dividerPositionPixels > hostWidth)
		dividerPositionPixels = hostWidth;
	if (dividerPositionPixels < 0)
		dividerPositionPixels = 0;
	
	return dividerPositionPixels;
}

export const getRatios = (displayedPane, hostWidth, defaultDividerPosition, leftPaneConstraints, rightPaneConstraints) => {
	
	if (displayedPane == LEFT)
		return {
			leftPct: 100,
			rightPct: 0
		}
	if (displayedPane == RIGHT) {
		return {
			leftPct: 0,
			rightPct: 100
		}
	}
	const dividerPositionPixels = getPositionPixels(hostWidth, defaultDividerPosition);
	const {
		newLeftPct,
		newRightPct
	} = computePaneWidths(dividerPositionPixels, hostWidth, {leftPaneConstraints, rightPaneConstraints})
	return {
		leftPct: newLeftPct,
		rightPct: newRightPct
	}
}

export const getDividerPosition = (hostWidth, defaultDividerPosition, leftPaneConstraints, rightPaneConstraints) => {
	let dividerPositionPixels = getPositionPixels(hostWidth, defaultDividerPosition);

	const {
		newLeftPct
	} = computePaneWidths(dividerPositionPixels, hostWidth, {leftPaneConstraints, rightPaneConstraints});
	return newLeftPct;
}


export const convertToPctWithConstraints = (newLeftPixels, newRightPixels, minLeftPixels, minRightPixels, containerWidthPixels) => {
	let newLeftPct, newRightPct;

	if (newLeftPixels < minLeftPixels) {
		newLeftPixels = minLeftPixels;
		newLeftPct = convertPixelsToPct(newLeftPixels, containerWidthPixels);
		newRightPct = 100 - newLeftPct;
	}
	else if (newRightPixels < minRightPixels) {
		newRightPixels = minRightPixels;
		newRightPct = convertPixelsToPct(newRightPixels, containerWidthPixels);
		newLeftPct = 100 - newRightPct;
	} else {
		newLeftPct = convertPixelsToPct(newLeftPixels, containerWidthPixels);
		newRightPct = 100 - newLeftPct;
	}

	return {
		newLeftPct,
		newRightPct
	}
}

export const convertPixelsToPct = (panePx, containerWidthPixels) => {
	if (containerWidthPixels < 1 || panePx < 1) return 0;
	if (panePx > containerWidthPixels) return 100;
	return (panePx / containerWidthPixels) * 100;
}

export const computePaneWidths = (dividerPositionPixels, containerWidthPixels, paneWidthConstraints) => {
	const {leftPaneConstraints, rightPaneConstraints} = paneWidthConstraints;
	const minLeftPixels = leftPaneConstraints.minPixels;
	const minLeftPct = leftPaneConstraints.minRatio
	const minRightPixels = rightPaneConstraints.minPixels;
	const minRightPct = rightPaneConstraints.minRatio;

	let newLeftPixels = dividerPositionPixels;
	let newRightPixels = containerWidthPixels - DIVIDER_BAR_WIDTH - newLeftPixels;
	let {newRightPct, newLeftPct} = convertToPctWithConstraints(newLeftPixels, newRightPixels, minLeftPixels, minRightPixels, containerWidthPixels)
	if (newLeftPct < minLeftPct) {
		newLeftPct = minLeftPct;
		newRightPct = 100 - newLeftPct;
	} else if (newRightPct < minRightPct) {
		newRightPct = minRightPct;
		newLeftPct = 100 - newRightPct;
	}

	return {
		newLeftPct,
		newRightPct
	}
}
