import { createCustomElement, actionTypes } from "@servicenow/ui-core";
import snabbdom from "@servicenow/ui-renderer-snabbdom";
import style from "./styles.scss";
import { view } from "./view";
import { LEFT, RIGHT, BOTH, INIT, USER_ACTION, PROP_CHANGE } from "./constants";
import {
	LEFT_BTN_CLICKED,
	MIDDLE_BTN_CLICKED,
	RIGHT_BTN_CLICKED,
	PANE_SIZES_CHANGE_REQUESTED,
	PANE_SIZES_CHANGED,
	PANE_DISPLAYED_CHANGE_REQUESTED,
	PANE_DISPLAYED_CHANGED
} from "./actions";

import { computePaneWidths, getRatios, getDividerPosition } from "./helpers";

const { COMPONENT_CONNECTED, COMPONENT_PROPERTY_CHANGED } = actionTypes;

/**
 *
 * Basic usage:
 *
 * ```
 * <now-resizable-panes/>
 * 	<my-component-a slot="left"/>
 * 	<my-component-b slot="right"/>
 * </now-resizable-panes>
 * ```
 *
 * @seismicElement now-resizable-panes
 * @summary Component that takes in two components: one in the left slot and one in the right slot and displays them in two vertically separated resizable panes.
 **/
createCustomElement("now-resizable-panes", {
	renderer: { type: snabbdom },
	view,
	styles: style,
	initialState: {
		isDragging: false,
		displayedPane: "",
		rightPct: 0,
		leftPct: 0,
		host: {}
	},
	properties: {
		/**
		 * Prevents the user from resizing panes with the divider.
		 * @type {boolean}
		 */
		disabled: {
			default: false,
			schema: {
				type: "boolean"
			},
		},
		/**
		 * Displays an action bar on the bottom to allow user to switch between display modes.
		 * @type {boolean}
		 */
		actionBar: {
			default: false,
			schema: {
				type: "boolean"
			},
		},
		/**
		 * Defines icons and tooltips used by the action bar to switch between different view modes.
		 * @type {ActionBarConfig}
		 */
		actionBarConfig: {
			default: {
				leftIcon : "record-activity-details-fill",
				middleIcon: "record-activity-stream-outline",
				rightIcon : "record-activity-outline",
				leftTooltip : "",
				middleTooltip : "",
				rightTooltip : "",
			},
			schema: {
				type: "object",
				properties: {
					leftIcon : {
						type: "string"
					},
					middleIcon : {
						type: "string"
					},
					rightIcon : {
						type: "string"
					},
					leftTooltip : {
						type: "string"
					},
					middleTooltip : {
						type: "string"
					},
					rightTooltip : {
						type: "string"
					},
				}
			},
		},
		/**
		 * Default position for the divider. A number between 0 and 100.
		 * @type {number}
		 */
		defaultDividerPosition: {
			default: "50",
			schema: {
				anyOf: [
					{
						type: "number"
					},
					{
						type: "string"
					}
				]
			}
		},
		/**
		 * Custom css styles for the divider expressed in JSON
		 * @type {object}
		 */
		dividerStyles: {
			default: {},
			schema: {type: "object"},
		},
		/**
		 * Defines the panes it should display when it renders on a page for the first time
		 * @type {"LEFT" | "BOTH" | "RIGHT"}
		 */
		defaultDisplayedPane: {
			default: BOTH,
			schema: {
				type: "string",
			},
		},
		/**
		 * When true, no borders will be displayed
		 * @type {boolean}
		 */
		bare: {
			default: false,
			schema: {
				type: "boolean",
			},
		},
		/**
		 * Defines width size constraints for the left pane with a min size ratio (between 0 and 100) and/or a min pixel size.
		 * @type {PaneConstraints}
		 */
		leftPaneConstraints: {
			default: {
				minRatio: 0,
				minPixels: 0
			},
			schema: {
				type: "object",
				properties: {
					minRatio: {
						dividerPosition: {
							anyOf: [
								{
									type: "number"
								},
								{
									type: "string"
								}
							]
						}
					},
					minPixels: {
						dividerPosition: {
							anyOf: [
								{
									type: "number"
								},
								{
									type: "string"
								}
							]
						}
					},
				}
			},
		},
		/**
		 * Defines width size constraints for the right pane with a min size ratio (between 0 and 100) and/or a min pixel size.
		 * @type {PaneConstraints}
		 */
		rightPaneConstraints: {
			default: {
				minRatio: 0,
				minPixels: 0,
			},
			schema: {
				type: "object",
				properties: {
					minRatio: {
						type: "number",
					},
					minPixels: {
						type: "number",
					},
				}
			}
		},
	},
	actions: {
		[PANE_SIZES_CHANGE_REQUESTED]: {
			schema: {
				type: 'object',
				properties: {
					dividerPosition: {
						anyOf: [
							{
								type: "number"
							},
							{
								type: "string"
							}
						]
					}
				}
			}
		},
		[PANE_DISPLAYED_CHANGE_REQUESTED]: {
			schema: {
				type: 'object',
				properties: {
					displayedPane: {type: 'string'},
				}
			}
		},
		[PANE_SIZES_CHANGED]: {
			schema: {
				type: 'object',
				properties: {
					dividerPosition: {
						anyOf: [
							{
								type: "number"
							},
							{
								type: "string"
							}
						]
					}
				}
			}
		},
		[PANE_DISPLAYED_CHANGED]: {
			schema: {
				type: 'object',
				properties: {
					displayedPane: {type: 'string'},
				}
			}
		},
	},
	actionHandlers: {
		[LEFT_BTN_CLICKED]: {
			effect({ dispatch }) {
				dispatch(PANE_DISPLAYED_CHANGE_REQUESTED, {displayedPane: LEFT, interaction: USER_ACTION})
			},
			stopPropagation: true,
		},
		[RIGHT_BTN_CLICKED]: {
			effect({ dispatch }) {
				dispatch(PANE_DISPLAYED_CHANGE_REQUESTED, {displayedPane: RIGHT, interaction: USER_ACTION})
			},
			stopPropagation: true,
		},
		[MIDDLE_BTN_CLICKED]: {
			effect({ dispatch }) {
				dispatch(PANE_DISPLAYED_CHANGE_REQUESTED, {displayedPane: BOTH, interaction: USER_ACTION})
			},
			stopPropagation: true,
		},
		[PANE_SIZES_CHANGE_REQUESTED]: {
			effect({action, updateState, dispatch}) {
				const { dividerPosition, interaction = "" } = action.payload;
				updateState({
					displayedPane: BOTH,
					leftPct: dividerPosition,
					rightPct: 100 - dividerPosition,
				});
				if (interaction !== INIT)
					dispatch(PANE_SIZES_CHANGED, {dividerPosition: action.payload.dividerPosition, interaction})
			},
			stopPropagation: true
		},
		[PANE_DISPLAYED_CHANGE_REQUESTED]: {
			effect({action, updateState, state, dispatch}) {
				const { displayedPane, interaction = "" } = action.payload;
				const hostWidth = state.host.clientWidth;
				
				if (state.displayedPane !== displayedPane) {
					const { defaultDividerPosition, leftPaneConstraints, rightPaneConstraints} = state.properties;
					const { leftPct, rightPct } = getRatios(displayedPane, hostWidth, defaultDividerPosition, leftPaneConstraints, rightPaneConstraints);
					updateState({
						displayedPane,
						leftPct,
						rightPct
					})
					if (interaction !== INIT)
						dispatch(PANE_DISPLAYED_CHANGED, {displayedPane, interaction})
				}
			},
			stopPropagation: true
		},
		[COMPONENT_CONNECTED]: {
			effect: ({ state, updateState, dispatch, action}) => {
				const { host } = action.payload;
				const { defaultDividerPosition, defaultDisplayedPane, leftPaneConstraints, rightPaneConstraints } = state.properties;
				updateState({
					host,
					shouldRender: false
				})
				// when we nest resizable-pane-components the width of the nested component is not known right away. The parent
				// has to resolve the size first, then the child knows how big it should be. We need to know this information in
				// order to calcuate the default position of the divider. The setTimeout was introduced to add a delay to allow components
				// to resolve, however a 300ms limit was introduced. If the parent components do not resolve their sizes within 300 sec we
				// default to just showing whatever the default divider position is, even if it violates constraints.
				let count = 0;
				if (defaultDisplayedPane == LEFT || defaultDisplayedPane == RIGHT) {
					dispatch(PANE_DISPLAYED_CHANGE_REQUESTED, {displayedPane: defaultDisplayedPane, interaction: INIT})
				} else {
					const sizeInterval = setInterval(() => {
						if (host.clientWidth > 0 || count > 30) {
							let dividerPosition = defaultDividerPosition;
							if (host.clientWidth !== 0)
								dividerPosition = getDividerPosition(host.clientWidth, defaultDividerPosition, leftPaneConstraints, rightPaneConstraints)
							dispatch(PANE_SIZES_CHANGE_REQUESTED, {dividerPosition, interaction: INIT})
							clearInterval(sizeInterval);
						} else {
							count++;
						}
					}, 10);
				}
			}
		},
		[COMPONENT_PROPERTY_CHANGED]: {
			effect({ state, action, dispatch }) {
				const { host } = action.payload;
				if (action.payload.name == "defaultDividerPosition") {
					const {leftPaneConstraints, rightPaneConstraints} = state.properties;
					const { value } = action.payload;
					const dividerPosition = getDividerPosition(host.clientWidth, value, leftPaneConstraints, rightPaneConstraints)
					dispatch(PANE_SIZES_CHANGE_REQUESTED, {dividerPosition, interaction: PROP_CHANGE})
				}
				else if (action.payload.name == "defaultDisplayedPane") {
					dispatch(PANE_DISPLAYED_CHANGE_REQUESTED, {displayedPane: action.payload.value, interaction: PROP_CHANGE})
				}
			}
		}
	},
	eventHandlers: [
		{
			events: ["mousedown"],
			effect({ state, updateState, action }) {
				const { disabled } = state.properties;
				if (!state.isDragging && !disabled) {
					const { event, host } = action.payload;
					if (
						event.composedPath().some((el) => el.className == "divider-bar") &&
						event.target == host
					) {
						event.preventDefault();
						updateState(() => {
							return {
								path: "isDragging",
								value: true,
								operation: "assign",
							};
						});
					}
				}
			},
		},
		{
			events: ["mouseup"],
			target: document,
			effect({
				updateState, dispatch, state
			}) {
				const {dividerPosition} = state;
				const {disabled, defaultDividerPosition} = state.properties;
				if (!disabled) {
					updateState(() => {
						if (state.isDragging) {
							const newDividerPosition = dividerPosition === undefined ? defaultDividerPosition : dividerPosition;
							dispatch(PANE_SIZES_CHANGED, {dividerPosition: newDividerPosition , interaction: USER_ACTION})
						}
						return {
							path: "isDragging",
							value: false,
							operation: "assign",
						};
					});
				}
			},
		},
		{
			events: ["mousemove"],
			target: document,
			effect({
				state,
				updateState,
				dispatch,
				action: {
					payload: { event, host },
				},
			}) {
				const { disabled } = state.properties;
				if (state.isDragging && !disabled) {
					event.preventDefault();

					const {
						leftPaneConstraints,
						rightPaneConstraints,
					} = state.properties;

					const dividerPositionPixels = event.x - host.getBoundingClientRect().left;
					const containerWidthPixels = host.clientWidth;

					const {newLeftPct, newRightPct} = computePaneWidths(dividerPositionPixels, containerWidthPixels, {leftPaneConstraints, rightPaneConstraints} )

					/* We are calling updateState here instead of dispatching an action to do so
					 * to prevent dispatching too many actions causing performance problems
					 */
					updateState({
						leftPct: newLeftPct,
						rightPct: newRightPct,
						dividerPosition: newLeftPct,
					});
				}
			},
		},
	],
});
